  <div class="d-flex flex-column align-center justify-center overview">
    <center><div ref="name" class="text-h4">GHEMID Mohamed</div></center>

    <div ref="position" class="text-h5 mb-8 text--secondary">
      Software Developer
    </div>

    <div class="d-flex align-center justify-center mt-2">
      <div ref="craftsman" class="software-title text-h5 mb-8  mr-2">
