<script>
import anime from "animejs";
import projects from "../data/projectsData";
import career from "../data/careerData";

export default {
  mounted() {
    this.animate();
    this.animateSoftwareTitle();
    this.animateName();
  },
  methods: {
    animateName() {
      let timeline = anime.timeline({
        easing: "linear",
        loop: true,
        duration: 3000,
      });

      timeline.add({
        targets: this.$refs.name,
        color: ["rgba(255, 255, 255, 100)", "rgba(0, 188, 212, 100)"],
      });
      timeline.add({
        targets: this.$refs.name,
        color: ["rgba(0, 188, 212, 100)", "rgba(255, 255, 255, 100)"],
      });
    },
    animate() {
      this.timelineAnimation = anime.timeline({
        easing: "spring(1, 80, 10, 0)",
      });
      this.timelineAnimation.add({
        targets: this.$refs.name,
        translateY: [-100, 0],
      });
      this.timelineAnimation.add(
        {
          targets: this.$refs.position,
          translateY: [1000, 0],
        },
        "-=500"
      );
      this.timelineAnimation.add(
        {
          targets: this.$refs.centerImg,
          translateY: [1000, 0],
          duration: 2000,
        },
        "-=600"
      );
      this.timelineAnimation.add(
        {
          targets: [
            this.$refs.webSkill,
            this.$refs.mobileSkill,
            this.$refs.projectCounter,
          ],
          translateX: [-1000, 0],
        },
        "-=600"
      );
      this.timelineAnimation.add(
        {
          targets: [
            this.$refs.embeddedSkill,
            this.$refs.designSkill,
            this.$refs.diplomaCounter,
          ],
          translateX: [1000, 0],
        },
        "-=600"
      );

      this.timelineAnimation.add(
        {
          targets: this.$refs.projectCounter,
          value: [0, projects.length],
          round: 1,
          easing: "easeInOutExpo",
          duration: 3000,
        },
        "-=1000"
      );

      const diplomaNumber = career.filter(
        (elt) => elt.type === "education" || elt.type === "certification"
      ).length;
      this.timelineAnimation.add(
        {
          targets: this.$refs.diplomaCounter,
          value: [0, diplomaNumber],
          round: 1,
          easing: "easeInOutExpo",
          duration: 3000,
        },
        "-=3100"
      );
    },
    animateSoftwareTitle() {
      console.log({ refs: this.$refs });
      let timeline = anime.timeline({
        easing: "easeOutExpo",
        loop: true,
      });

      timeline.add({
        targets: this.$refs.craftsman,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.craftsman,
        translateY: [0, 20],
        opacity: [1, 0],
      });

      timeline.add({
        targets: this.$refs.agile,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.agile,
        translateY: [0, 20],
        opacity: [1, 0],
      });

      timeline.add({
        targets: this.$refs.fullstack,
        translateY: [-20, 0],
        opacity: [0, 1],
      });
      timeline.add({
        targets: this.$refs.fullstack,
        translateY: [0, 20],
        opacity: [1, 0],
      });
    },
  },
  data() {
    return {
      timelineAnimation: null,
    };
  },
  computed: {
    isMobile() {
      return this.$vuetify.breakpoint.xs;
    },
  },
};
</script>

